/*------------popup--------*/
body.active-modal {
    @apply overflow-y-hidden;
  }
  
  .btn-modal {
    padding: 10px 20px;
    @apply block text-lg;
    margin: 100px auto 0;
  }
  
  .modal,
  .overlay {
    /* width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: fixed;
    z-index: 9999; */

    @apply w-screen h-screen fixed top-0 left-0 right-0 bottom-0 z-[9999];
  }
  
  .overlay {
    @apply bg-[#ecf0f3] dark:bg-slate-500;
  }
  .modal-content {
    /* box-shadow: 4px 4px 8px #cbcbcb, -4px -4px 8px #ffffff; */
    @apply shadow-2xl shadow-slate-400 dark:shadow-slate-800 bg-gradient-to-tr from-[#e2e8ec] to-[#ffffff];
    padding: 10px;
    @apply absolute top-[12%] left-[5%] leading-6 bg-[#f0f3f5] dark:bg-slate-500 px-4 py-7 max-w-[90%] min-w-[60%] z-[9999] rounded-xl;
  }
  
  .modal-text {
    padding: 0 100px 0 50px;
  }
  .modal-text h1 {
    margin: 20px 0 20px 0;
    font-size: 30px;
  }
  .modal-text p {
    font-size: 17px;
    margin-bottom: 20px;
    line-height: 25px;
  }
  .modal-text button {
    margin-right: 20px;
    @apply text-teal-500;
  }
  .modal-text button i {
    margin-left: 10px;
  }
  .modal-text button.close-modal {
    position: absolute;
    top: 10px;
    right: -10px;
    width: 40px;
    height: 40px;
    text-align: center;
    border-radius: 50%;
    padding: 0 0;
    font-size: 20px;
  }
  .modal-text button.close-modal i {
    margin: 0;
  }
  /*------------popup--------*/
  @media (max-width: 768px) {
    .modal,
    .overlay {
      overflow: scroll;
    }
  
    .modal .modal-img {
      margin: 10px 0 10px 0;
    }
    .modal-content {
      flex-direction: column;
    }
    .modal .right,
    .modal .left {
      padding: 0;
    }
  }